var defaultAppState = {
  activeComponent: null,
  windowWidth: null,
};

export function appReducer(state = defaultAppState, action) {
  switch (action.type) {
    case 'UPDATE_APP':
      return {
        ...state,
        ...action.updates
      };

    case 'RESET_APP':
      return defaultAppState;

    case 'LOGOUT':
      return {
        ...state,
        ...defaultAppState
      };

    default:
      return state;
  }
}



