/**
* @description : This is the base component for all components which will use material-ui library.
* The reason of having this is because of having the ability to change some of material-ui
* props like isRtl, primaryColor, ... for all of the components at once.
*
* @method getChildContext
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import PropTypes from 'prop-types';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
// import lightBaseTheme from 'material-ui/styles/baseThemes/lightBaseTheme';

class BaseComponent extends React.Component {
  getChildContext() {
    return { muiTheme: getMuiTheme({ isRtl: true }) }; //lightBaseTheme
  }
}

BaseComponent.childContextTypes = {
  muiTheme: PropTypes.object
};

export default BaseComponent;



