/**
* @description : This components renders a progress loader on the screen
*
* @prop style (object) : Root styles
* @prop logoWidth (number) : Custome width for logo image
* @prop logoHeight (number) : Custome height for logo image
* @prop progressWidth (number) : Custome width for progress element
*
* @method getChildContext
* @method render
*
* @author Armin Eslami
* @version 1.0.0
*/

import React from 'react';
import PropTypes from 'prop-types';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import LinearProgress from 'material-ui/LinearProgress';

import BaseComponent from 'components/base/BaseComponent';
import Logo from 'components/Logo';
import './Style.css';

export class Loading extends BaseComponent {
  /**
  * @description : Sets rtl to false so the linear progress direction be left to right
  *
  * @author Armin Eslami
  *
  * @return material-ui theme object
  */
  getChildContext() {
    return { muiTheme: getMuiTheme({ isRtl: false }) };
  }

  render() {
    return (
      <div style={{ textAlign: 'center', ...this.props.style}}>
        <div className="wander">
          <Logo />
        </div>
        <div
          style={{
            width: this.props.progressWidth ? this.props.progressWidth : 56,
            margin: 'auto',
            direction: 'ltr'
          }}
        >
          <LinearProgress
            mode="indeterminate"
            style={{ direction: 'ltr', marginTop: 8 }}
          />
        </div>
      </div>
    );
  }
}

Loading.propTypes = {
  style: PropTypes.object,
  logoWidth: PropTypes.number,
  logoHeigh: PropTypes.number,
  progressWidth: PropTypes.number
};

export default Loading;



