/**
* @description : Updates app state
*
* @author Armin Eslami
*
* @param updates (object) : An object containing new app state
*
* @return action object for AppReducers
*/
export function updateApp(updates) {
  return {
    type: 'UPDATE_APP',
    updates
  };
}
  
/**
* @description : Restes all application states on app store
*
* @author Armin Eslami
*
* @return action object for AppReducers
*/
export function resetApp() {
  return {
    type: 'RESET_APP'
  };
}

/**
* @description : Creates an action object to clear app states
*
* @author Armin Eslami
*
* @return action object for AppReducers
*/
export function logout() {
    return {
      type: 'LOGOUT'
    };
}


