import React from 'react';
import ReactDOM from 'react-dom';
import registerServiceWorker from './registerServiceWorker';
import injectTapEventPlugin from 'react-tap-event-plugin';
import { Route, Router, IndexRoute, hashHistory } from 'react-router';
import { Provider } from 'react-redux';
import Loadable from 'react-loadable';
import cookie from 'react-cookie';

import 'styles/MainStyles.css';
import 'styles/Animations.css';
import { updateApp, logout } from './redux/actions/AppActions';
import Loading from './components/loading/Loading';
var store = require('./redux/store/Config').config();
// This loading is showed to users while each componet chunk is downloading
var LoadingComponent = <Loading style={{ marginTop: '15%' }} />;
const Main = Loadable({
  loader: () => import('./components/Main'),
  loading: () => LoadingComponent
});
const Login = Loadable({
  loader: () => import('./components/login/Login'),
  loading: () => LoadingComponent
});
const Accounts = Loadable({
  loader: () => import('./components/accounts/Accounts'),
  loading: () => LoadingComponent
});
const Dashboard = Loadable({
  loader: () => import('./components/dashboard/Dashboard'),
  loading: () => LoadingComponent
});
const Profile = Loadable({
  loader: () => import('./components/profile/Profile'),
  loading: () => LoadingComponent
});
const InvalidRoute = Loadable({
  loader: () => import('./components/InvalidRoute'),
  loading: () => null
});

// The App state object
var state = store.getState();

// Change active component based on current route path
var activeComponent = hashHistory.getCurrentLocation().pathname;

// Dispatch an action to set new active component on app state
store.dispatch(updateApp({ activeComponent }));

// Subscribe to state changes for debug
store.subscribe(() => {
  // NOTE: This line is important
  state = store.getState();
  process.env.NODE_ENV === 'development'
    ? console.log('New State', state)
    : void 0;

  // If there is no token, log out the user
  if (
    state.user &&
    state.user.token === null &&
    state.user.isLoggedIn === true
  ) {
    cookie.remove('_wr', { path: '/' });
    store.dispatch(logout());
  }

  // If user state exists and user is logged in, then redirect to the the main path or current active component
  if (state.user && state.user.isLoggedIn) {
    if (hashHistory.getCurrentLocation().pathname === '/login') {
      if (
        activeComponent === '/login' ||
        activeComponent === '/accounts' ||
        activeComponent === null
      ) {
        hashHistory.replace('/');
      } else {
        hashHistory.replace(activeComponent);
      }
    }
  } else if (
    state.user.universities &&
    state.user.universities.length > 1 &&
    hashHistory.getCurrentLocation().pathname === '/accounts'
  ) {
    process.env.NODE_ENV === 'development'
      ? console.log('User has entered to the accounts route')
      : void 0;
  } else if (
    state.user &&
    !state.user.isLoggedIn &&
    hashHistory.getCurrentLocation().pathname !== '/login' &&
    !cookie.load('_wr')
  ) {
    // If user is not logged in and also is not at login route, then redirect to the lgoin route
    hashHistory.replace('login');
  }
});

// A listener for route change.
hashHistory.listen(function(ev) {
  // TODO: Change hashHistory to browserHistory
  if (hashHistory.getCurrentLocation().pathname !== '/login') {
    activeComponent = hashHistory.getCurrentLocation().pathname;
    store.dispatch(updateApp({ activeComponent }));
  }
});

/**
* @description : Checks if user is logged redirects him/her to / route
*
* @author Armin Eslami
*
* @param nextState (object) : Contains next state of app
* @param replace (func) : Can be used to change react-router route
* @param next (func) : Allows the process to countinue
*/
function redirectIfLoggedIn(nextState, replace, next) {
  state = store.getState();
  if (state.user && state.user.isLoggedIn) {
    replace('/');
  }
  next();
}

/**
* @description : Checks if user is not login redirect him/her to /login route.
*
* @author Armin Eslami
*
* @param nextState (object) : Contains next state of app
* @param replace (func) : Can be used to change react-router route
* @param next (func) : Allows the process to countinue
*/
function requireLogin(nextState, replace, next) {
  state = store.getState();
  if (state.user && !state.user.isLoggedIn && !cookie.load('_wr')) {
    replace('/login');
  }
  next();
}

/**
* @description : Checks if user has more than one universites or not and if not, 
* stops him/her from entering to /accounts
*
* @author Armin Eslami
*
* @param nextState (object) : Contains next state of app
* @param replace (func) : Can be used to change react-router route
* @param next (func) : Allows the process to countinue
*/
function redirectIfHasOneAccount(nextState, replace, next) {
  state = store.getState();
  if (
    !state.user ||
    !state.user.universities ||
    state.user.universities.length === 1
  ) {
    process.env.NODE_ENV === 'development'
      ? console.log('No right to enter /accounts and re-directing to /')
      : void 0;
    replace('/');
  }
  next();
}

injectTapEventPlugin();
ReactDOM.render(
  <Provider store={store}>
    <Router history={hashHistory}>
      <Route path="login" component={Login} onEnter={redirectIfLoggedIn} />

      <Route
        path="accounts"
        component={Accounts}
        onEnter={redirectIfHasOneAccount}
      />

      <Route path="/" component={Main} onEnter={requireLogin}>
        <IndexRoute component={Dashboard} onEnter={requireLogin} />
        <Route path="profile" component={Profile} onEnter={requireLogin} />
      </Route>

      <Route path="*" component={InvalidRoute} />
    </Router>
  </Provider>,
  document.getElementById('app')
);
registerServiceWorker();



