var defaultUserState = {
  token: null,
  isLoggedIn: false,
};
  
export function userReducer(state = defaultUserState, action) {
  switch (action.type) {
    case 'UPDATE_USER':
      return {
        ...state,
        ...action.userData
      };

    case 'INSERT_USER_DATA':
      return {
        ...action.userData
      };

    case 'LOGOUT':
      return defaultUserState;

    default:
      return state;
  }
}


